import { h,Fragment, render } from 'preact';
import { useState } from 'preact/hooks';
import style from './style.css';
import Response  from '../../components/response_view';
import Quote from '../../components/result_quote';

const dafautData = {
	answer : "",
	data:[
	]
}

const Home = () => {
	const [query, setQuery] = useState("");
	const [responseData, setResponseData] = useState(dafautData)
	const [loading, setLoading] = useState(false)
	
	const onRequest = () => {
		setLoading(true);
		fetch(`http://0.0.0.0:1234/search?query=${query}`)
			.then((response) => response.json())
			.then((data) =>{
				setResponseData(data);
				setLoading(false);
			} );
	}
	
	const responseComponents = responseData.data.map(
		v=><Quote key={v.text} data={v} />
	)
	const response = <Response answer={responseData.answer}/>
	
	return <div class={style.home}>
		<div class={style.body}>
			<div class="input-group mb-3">
				<input disabled={loading} type="text" class="form-control" placeholder="Ваш запрос" aria-label="Ваш запрос" aria-describedby="basic-addon2" value={query} onChange={(e) => {setQuery(e.target.value)}}/>
				<div class="input-group-append">
					<button disabled={loading} class="btn btn-outline-secondary" type="button" onClick={onRequest}>Отправить</button>
				</div>
			</div>
			{loading && <div class="d-flex justify-content-center"><div class="spinner-border text-primary c" role="status">
						<span class="visually-hidden">Loading...</span>
						</div></div>}
			{!loading && response}
			<div class={"overflow-scroll vh-50"}>
				{!loading && responseComponents}
			</div>
		</div>
	</div>	
};

export default Home;
